define('ermes-smart-app/controllers/index/observation', ['exports', 'ember', 'ermes-smart-app/mixins/product-upload'], function (exports, Ember, ProductUpload) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend(ProductUpload['default'], {
    panelId: 'observation',
    i18n: Ember['default'].inject.service(),
    actions: {
      submit: function submit() {
        if (!this.get('files')) {
          this.set('fileError', this.get('i18n').t('panel.notification.file-missing'));
        } else {
          this.set('fileError', '');
          // Manage file upload
          this._super(this);
        }
      }
    }
  });

});