define('ermes-smart-app/mixins/product-upload-rdate', ['exports', 'ember', 'ermes-smart-app/mixins/product-upload'], function (exports, Ember, ProductUpload) {

  'use strict';

  exports['default'] = Ember['default'].Mixin.create(ProductUpload['default'], {
    actions: {
      submit: function submit() {
        if (!this.get('model.date')) {
          this.set('dateError', this.get('i18n').t('panel.notification.missing-date'));
        } else {
          this.set('dateError', '');
          this._super(this);
        }
      }
    }
  });

});