define('ermes-smart-app/routes/index/profile', ['exports', 'ember', 'ermes-smart-app/mixins/auth-checker', 'ermes-smart-app/mixins/panel-manager'], function (exports, Ember, AuthChecker, PanelManager) {

  'use strict';

  exports['default'] = Ember['default'].Route.extend(AuthChecker['default'], PanelManager['default'], {
    model: function model() {
      var username = this.get('auth').getCurrentUserId();
      return this.store.findRecord('user', username);
    }
  });

});